@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@100;300;400;700&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
:root {
  --background: #fff;
  --cards: #cee;
  --text: #000;
}
body.theme-lightmode{
  --background: #fff;
  --cards: #cee;
  --text: #000;
}
body.theme-darkmode{
  --background: #000;
  --cards: #444;
  --text: #fff;
}
* {
  margin: 0;
  padding: 0;
  font-family: "Antonio", sans-serif;
}
body {
  background-color: #fff;
  background-color: var(--background);
}
h1 {
  color: #000;
  color: var(--text);
  font-size: 40px;
  font-weight: 600;
}
h3 {
  color: #000;
  color: var(--text);
  text-align: center;
  font-weight: 600;
  margin: 10px 0;
}
p {
  color: #000;
  color: var(--text);
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  padding: 10px;
}
.row {
  margin-top: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.container-2 {
  grid-template-columns: 1fr 1fr;
  display: grid;
  grid-gap: 30px;
  padding: 15px;
}
.container-3 {
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
  grid-gap: 30px;
  padding: 15px;
}
div.courses {
  min-height: 100vh;
  width: 80%;
  margin: auto;
  text-align: center;
}

/* header section */

.hero-btn {
  /* display: inline-block; */
  text-decoration: none;
  color: #fff;
  border: 1px solid #fff;
  padding: 13px 34px;
  font-size: 16px;
  background: transparent;
  position: relative;
  cursor: pointer;
  border-radius: 4px;
  margin: 2px 12px;
}
.hero-btn:hover {
  border: 1px solid #cee;
  border: 1px solid var(--cards);
  background: #fff;
  background: var(--background);
  transition: 0.3s;
}
.hero-btn .fa {
  margin: 0 4px;
}
.gray-btn {
  border: 2px solid white;
  /* background: transparent; */
  color: #3c415c;
}

.gray-btn:hover {
  color: #fff;
}



