.about-us {
  width: 80%;
  margin: auto;
  padding-top: 80px;
  padding-bottom: 50px;
}
.about-col {
  -webkit-flex-basis: 48%;
          flex-basis: 48%;
  padding: 30px 2px;
}
.about-col img {
  width: 100%;
}
.about-col h1 {
  padding-top: 0;
}
.about-col p {
  padding: 15px 0 25px;
}


@media (max-width: 1200px) {
  .container-3 {
    grid-template-columns: 1fr 1fr;
    display: grid;
    grid-gap: 30px;
    padding: 15px;
  }
}

@media (max-width: 750px) {
  p{
    font-size: 13px;
  }
  h1{
    font-size: 28px;
  }
  .row {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .container-2,  .container-3 {
    margin-top: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
