:root {
    --nav_link_under_line: #2873cf;
    --background: #fff;
    --cards: #cee;
    --text: #000;
  }

  header i.fa.fa-moon-o{
    font-size: 26px;
  }
  body.theme-darkmode header i.fa.fa-moon-o{ 
    color: #fff;
  }
  body.theme-lightmode header i.fa.fa-moon-o{
    color: #000;
  }
  header .fa {
    /* color: #e94560; */
    margin: 0 13px;
    cursor: pointer;
    padding: 18px 0;
  }
  header .min-header {
    width: 100%;
    background-position: center;
    background-size: cover;
    text-align: center;
    color: #fff;
  }
  header .min-header h1 {
    margin-top: 20px;
  }
  header nav {
    display: -webkit-flex;
    display: flex;
    padding: 1% 4%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
  header nav img {
    width: 43px;
  }
  header .nav-links {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;
  }
  header .nav-links ul li {
    list-style: none;
    display: inline-block;
    padding: 8px 12px;
    position: relative;
  }
  header .nav-links ul li a {
    /* color: #fff; */
    color: #000;
    text-decoration: none;
    font-size: 18px;
  }
  header .nav-links ul li::after {
    content: "";
    width: 0%;
    height: 2px;
    background: #2873cf;
    background: var(--nav_link_under_line);
    display: block;
    margin: auto;
    transition: 0.5s;
  }
  header .nav-links ul li:hover::after {
    width: 100%;
  }
  header nav i.fa.fa-bars, nav i.fa.fa-times {
    display: none;
  }
  body.theme-darkmode header h1, body.theme-darkmode header p, body.theme-darkmode header h3, body.theme-darkmode header a{
    color: #fff !important;
  }
  body.theme-lightmode header h1, body.theme-lightmode header p, body.theme-lightmode header h3, body.theme-lightmode header a{
    color: #000 !important;
  }
  /*temp start*/
  header h1,header p, header h3,  header a,header i.fa.fa-moon-o{
    color: #000 !important;
  }
  /*temp end*/

  @media (max-width: 750px) {
  
    header .nav-links ul li {
      display: block;
    }
    header .nav-links {
      position: fixed;
      background: #fff;
      background: var(--background); /*#000000;*/
      height: 100vh;
      width: 200px;
      top: 0;
      right: -200px;
      text-align: left;
      z-index: 2;
      transition: 0.7s;
      color: #000;
      color: var(--text);
    }
    header nav i.fa.fa-bars, header nav i.fa.fa-times {
      display: block;
      color: #000;
      color: var(--text);
      margin: 10px;
      font-size: 24px;
      cursor: pointer;
    }
  
    header .nav-links ul {
      padding: 30px;
    }
  
    header .min-header h1 {
      font-size: 24px;
    }
  
  }
div.courses {
  min-height: 100vh;
  width: 80%;
  margin: auto;
  text-align: center;
}



.courses {
  padding-top: 10px;
  color: var(--text);
}

.courses  .CourseCard{
  -webkit-flex-basis: 31%;
          flex-basis: 31%;
  background: var(--cards);
  border-radius: 10px;
  margin-bottom: 5%;
  padding: 20px 12px;
  box-sizing: border-box;
  transition: 0.3s;
}

.CourseCard:hover {
  box-shadow: 0 0 20px 0px var(--text);
}
.courses h1 {
  margin: 10px;
}
.courses p {
  margin: 10px;
}


.courses-container a {
  color: var(--text);
  text-decoration: none;
  font-size: 18px;
}

.courses-container .CourseCardLinks {
  color: #717171;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  padding: 15px;
}

.courses-container .CourseCardLinks .one-link {
  color: #717171;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  padding: 10px;
}


